.container{
    width: 100%;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
    header{
        height: 100%;
        width: 30%;
        background-color: white;
        position: fixed;

        display: flex;
        flex-direction: column;
        color:  rgb(228, 70, 57);
        justify-content: space-around;

        &>*{
            margin: 50px;
        }

        .title{
            font-size: 4.5rem;
        }

        .sub-title :nth-child(1){
            font-size: 2rem;
        }

        .third-party-link{
            display: flex;
            justify-content: space-around;
            a{
                color: grey;
            }
            text-decoration: underline;
        }
    }
    article{
        height: 100%;
        margin-left: 30%;
        background-color: rgb(228, 70, 57);

        display: flex;
        flex-direction: column-reverse;
        .movies{ 
            height: 669px;
            padding: 150px;
            color: white;

            display: flex;
            flex-direction: column;
            .movie-title{
                width: 100%;
                display: flex;
                justify-content: space-between;
                margin-bottom: 10px;
            }
            iframe{
                width: 100%;
                height: 80%;
            }
            .moive-hit{
                width: 100%;
               text-align: end;
               margin-top: 10px;
            }
        }
    }
}